import utils from '../utils';
import ui from './ui';
import user from './user';

// 
const TIMEOUT = 20000
const CNT_TYPE = {
    JSON: 'application/json'
}
export default (opt, appId) => {
    let extra = utils.obj2Hash({
        devicemodel: window.plus.device.model, // 
        devicevendor: window.plus.device.vendor, // 
    })
  
    opt = Object.assign({
        url: '',
        type: 'GET',
        cache: false,
        dataType: 'JSON',
        // contentType: '', // 
        data: {},
        success: function () {},
        error: function () {},
        headers: {}
    }, opt)

    if (opt.url.indexOf('?') > -1) { // 
        opt.url += ('&' + extra);
    }else{
        opt.url += ('?' + extra);
    }

    ajax(opt, appId);
}

/**
 * ajax
 * */
function ajax(options, appId) {
    options = options || {};
    options.type = (options.type || 'GET').toUpperCase();
    options.dataType = options.dataType || 'json';
    
    var params = utils.obj2Hash(options.data);
    //options.contentType 'application/json'
    if (CNT_TYPE.JSON === options.contentType) {
        params = options.data;
    }
    var xhr = new window.plus.net.XMLHttpRequest();

    // 
    xhr.timeout = TIMEOUT

    xhr.onreadystatechange = function() {
        // 
        if (xhr.readyState === 4) {
            var status = xhr.status;
            
            if (status >= 200 && status < 300) {
                try{
                    options.success && options.success(JSON.parse(xhr.responseText), xhr.responseXML);
                }catch (e){
                    options.error && options.error(status);
                }

                let _token = xhr.getResponseHeader('token')

                // token
                if ( _token ) {
                    user.setToken(_token)
                }
            } else {
                options.error && options.error(status);
            }
        }
    }

    // 
    xhr.ontimeout = function() {
        options.error && options.error(-1);
    }

    if (options.type == 'GET') {
        if(options.url.indexOf('?') > -1) { // 
            options.url += ('&' + params);
        }else{
            options.url += ('?' + params);
        }
        xhr.open('GET', options.url);
        xhr.setRequestHeader('token', user.getToken())
        xhr.setRequestHeader('appId', appId || '2')
        xhr.send(null);
    } else if (options.type == 'POST') {
        xhr.open('POST', options.url);
        
        xhr.setRequestHeader('Content-Type', options.contentType ? options.contentType : 'application/x-www-form-urlencoded');
        xhr.setRequestHeader('X-Requested-With', 'XMLHttpRequest');
        xhr.setRequestHeader('token', user.getToken());
        xhr.setRequestHeader('appId', appId || '2');
        xhr.send( params);
    }
}


