/**
 * native
 */

export default {

  /**
   * ToastOption:
      icon: (String  )
      duration: (String  )
      "long""short""long"3.5s"short"2s"short"
      align: (String  )
      "left""center""right""center"
      verticalAlign: (String  )
      "top""center""bottom""bottom"
   */
  toast (message, options) {
    window.plus.nativeUI.toast(message, options)
  },
  
  alert(message, cb) {
    window.plus.nativeUI.alert(message, cb);
  },

  /**
   *  message: ( String )  
      confirmCB: ( ConfirmCallback )  
      EventindexNumber
      title: ( String )  
      buttons: ( Array[ String ] )  
      confirmCB
   */
  confirm(message, confirmCB, title, buttons) {
    window.plus.nativeUI.confirm( message, confirmCB, title, buttons );
  },

  showWaiting() {
    return window.plus.nativeUI.showWaiting();
  },

  /**
   * nativeUI 
   * @params options {
   *  title: ''
   *  date: ''
   *  minDate: ''
   *  maxDate: ''
   * }
   */
  pickDate(done, fail, options) {
    plus.nativeUI.pickDate( done, fail || function(){}, options || {});
  },

  /**
   * nativeUI 
   * @params options {
   *  title: ''
   *  is24Hour: '24'
   *  popover: '{top:10;left:10;width:200;height:200;}webview'
   *  maxDate: ''
   * }
   */
  pickTime(done, fail, options) {
    plus.nativeUI.pickTime( 
      done, 
      fail || function(){}, 
      options || {is24Hour: true}
    );
  }

}


