export default {
  clear() {
    plus.push.clear();
  },
  /*
  * Click:
  * click
  */
  onClick(cb) {
    // 
    plus.push.addEventListener( "click", cb, false );
  },
  /*
  * receive
  * receive
  * IOS
  */
  onReceive(cb) {
    // 
    plus.push.addEventListener( "receive", cb, false );
    
  }
}


