<template>
 
  <mt-popup
      :visible.sync="edit.show"
      position="right"
      style="width: 100%;height: 100%;"
  >
    <topbar 
        fixed 
        :right="right"
        @back="hideModal" 
        :autoback=false 
        :title="edit.title">
    </topbar>
<div style="margin-top: 10px;">     
    <div v-show="edit.key === 'sex'">
      <mt-radio
        align="right"
        v-model="edit.form.sex"
        :value="edit.form.sex"
        :options="[{label: '', value: m}, {label: '', value: f},{label: '', value: s}]">
      </mt-radio>
    </div>

    <div v-show="edit.key === 'userNick'">
      <mt-field 
        :value.sync="edit.form.userNick" 
        label=""  
        placeholder="">
      </mt-field>
    </div>

    <div v-show="edit.key === 'signature'">
      <mt-field 
        :value.sync="edit.form.userSign" 
        label="" 
        type="textarea"
        placeholder="">
      </mt-field>
    </div>

    <div v-show="edit.key === 'birth'">
      <mt-field 
        :value.sync="edit.form.birthDay" 
        label="" 
        type="date"
        placeholder="">
      </mt-field>
    </div>

    <div v-show="edit.key === 'height'">
      <mt-field 
        :value.sync="edit.form.height" 
        label="" 
        type="number"
        placeholder="">
      </mt-field>
    </div>

    <div v-show="edit.key === 'weight'">
      <mt-field 
        :value.sync="edit.form.weight" 
        label="" 
        type="number"
        placeholder="">
      </mt-field>
    </div>
</div>    
</mt-popup>
</template>

<script>
  import S from 'service';
  import { Cell, Radio, Field, Popup } from 'mint-ui';
  import Topbar from 'modules/common/Topbar';

  const props = ['edit']
  const events = {
  };

  export default {
  	components: {
      Topbar,
      [Popup.name]: Popup, [Cell.name]: Cell ,[Radio.name]: Radio , [Field.name]: Field
    },
    props: props,
    data() {
      return {
        right: {
            text: '',
            handle: this.submit
        }
      }
    },
    methods: {
      init() {
      },
      hideModal() {
        this.edit.show = false
      },
      submit() {
        let _form = Object.assign({}, this.edit.form);
        this.edit.show = false
        S.user.putInfo(_form).then(ret => {
            
            if(ret.status==200){
             
              console.log("");
            }else{
              console.log("");
            }
        
          });

      }
    },
    ready() {
      this.init();
    }
  };   

</script>
<style type="text/css">
	
</style>


