import entry from 'entry';
import NAPI from 'nadapter';
import S from 'service';

import address from './data'

import Topbar from 'modules/common/Topbar';
import {Field, Picker, PickerSlot, Popup} from 'mint-ui';
import './style.less';
// import Vue from 'vue';
// import vueArea from 'vue-area';



module.exports = entry({
   // el: '#app',
  template: require('./index.tpl'),
  components: {
    Topbar,
    [Field.name]: Field,
    [Picker.name]: Picker,
    [PickerSlot.name]: PickerSlot,
    [Popup.name]: Popup,
    // vueArea
  },

  data() {
    return {
      right: {
        text: '',
        handle: this.save
      },

      title: '',

      popupVisible: false,
      // result: null,
      // show: false,
     
      addressSlots: [
        {
          flex: 1,
          values: Object.keys(address),
          className: 'slot1',
          textAlign: 'center'
        }, {
          divider: true,
          content: '-',
          className: 'slot2'
        }, {
          flex: 1,
          values: [''],
          className: 'slot3',
          textAlign: 'center'
        }, {
          divider: true,
          content: '-',
          className: 'slot2'
        },{
          flex: 1,
          values: [''],
          className: 'slot3',
          textAlign: 'center'
        }
      ],

      form: {
        addrId: null,
        recvAddr: '',
        recvCity: '',
        recvDistict: '',
        recvMobile: '',
        recvName: '',
        recvProv: '',
        isDefault: 0
      },
    };
  },

  methods: {
    init() {
       if(this.__getPageData().addrId) {
        this.form = this.__getPageData();
        this.title = '';
      }
    },
    onAddressChange(picker, values) {
      this.addressSlots[2].values = address[values[0]];
      this.form.addressProvince = values[0];
      this.form.addressCity = values[1];
    },
    save() {
      let _form = Object.assign({}, this.form);
      S.user.newaddress(_form).then(ret => {           
            if(ret.status==200){
              history.go(-1)
              console.log("");
            }else{
              console.log("");
            }       
          });
      
    },
    select() {
      this.form.isDefault = this.form.isDefault === 1 ? 0 : 1
    },
    showPopup(show) {
      this.popupVisible = show
    }
  },
  ready() {
    this.init();
  }
});





