<template>
  <a class="mint-cell">
    <span class="mint-cell-mask" v-if="isLink"></span>
    <div class="mint-cell-left">
      <slot name="left"></slot>
    </div>
    <div class="mint-cell-wrapper">
      <div class="mint-cell-title">
        <slot name="icon">
          <i v-if="icon" class="mintui" :class="'mintui-' + icon"></i>
        </slot>
        <slot name="title">
          <span class="mint-cell-text" v-text="title"></span>
          <span v-if="label" class="mint-cell-label" v-text="label"></span>
        </slot>
      </div>
      <div class="mint-cell-value" :class="{ 'is-link' : isLink }">
        <slot>
          <span v-text="value"></span>
        </slot>
      </div>
    </div>
    <div class="mint-cell-right">
      <slot name="right"></slot>
    </div>
    <i v-if="isLink" class="mint-cell-allow-right"></i>
  </a>
</template>

<script>

/**
 * mt-cell
 * @module components/cell
 * @desc 
 * @param {string} [icon] -  more, back .mintui-xxx
 * @param {string} [title] - 
 * @param {string} [label] - 
 * @param {boolean} [is-link=false] - 
 * @param {string} [value] - 
 * @param {slot} -  value,  value 
 * @param {slot} [title] -  title,  title 
 * @param {slot} [icon] -  icon,  icon 
 *
 * @example
 * <mt-cell title="" icon="back" is-link value=""></mt-cell>
 * <mt-cell title="" icon="back">
 *   <div slot="value"></div>
 * </mt-cell>
 */
export default {
  name: 'mt-cell',

  props: {
    icon: String,
    title: String,
    label: String,
    isLink: Boolean,
    value: String
  }
};
</script>

<style lang="css">
  @import "./style/var.css";
  @import "./style/border.css";
  
  @component-namespace mint {

    @component cell {
      @mixin border-top $color-grey;
      @mixin border-bottom $color-grey;
      background-color: $color-white;
      box-sizing: border-box;
      color: inherit;
      min-height: 39px;
      display: block;
      overflow: hidden;

      & + .mint-cell::after {
        content: none;
      }

      &::before {
        left: 0px;
      }

      &:last-child::before {
        left: 0;
      }
      .mint-cell-wrapper{
        font-size:11px;
      }

      @descendent wrapper {
        align-items: center;
        box-sizing: border-box;
        display: flex;
        font-size: 11px;
        min-height: inherit;
        padding: 0 15px 0px 15px;
        position: relative;
        width: 100%;
      }

      @descendent mask {
        &::after {
          background-color: #000;
          content: " ";
          opacity: 0;
          position: absolute 0;
        }

        &:active::after {
          opacity: .1;
        }
      }

      @descendent text {
        vertical-align: middle;
      }

      @descendent label {
        color: #888;
        display: block;
        font-size: 12px;
        margin-top: 6px;
      }

      img {
        vertical-align: middle;
      }

      @descendent title {
        flex: 1;
      }

      @descendent value {
        color: $cell-value-color;
        display: flex;
        align-items: center;

        @when link {
          margin-right: 10px;
        }
      }

      @descendent left {
        position: absolute;
        height: 100%;
        left: 0;
        transform: translate3d(-100%, 0, 0);
      }

      @descendent right {
        position: absolute;
        height: 100%;
        right: 0;
        top: 0;
        transform: translate3d(100%, 0, 0);
      }

      @descendent allow-right::after {
        border: solid 1px $border-color;
        border-bottom-width: 0;
        border-left-width: 0;
        content: " ";
        position: absolute 50% 15px * *;
        size: 6px;
        transform: translateY(-50%) rotate(45deg);
      }
    }
  }
</style>



