/**
 * 
 * @param appid [int] appid 2 1
 * @param checkUrl [string] 
 * @return function 
 */
import ajax from './ajax'
import utils from '../utils'
import page from './page'
import Config from '../../../config'

export default (appid, checkUrl, done) => {
	plus.runtime.getProperty(plus.runtime.appid, function (inf) {
		var wgtVer = inf.version;

		ajax({
			url: checkUrl + '?version=' + wgtVer,
			success: (ret) => {
				if (ret.status && ret.data.forceUpdate === 1) {
					plus.nativeUI.confirm('', function(e){
            if(e.index == 1){
              downWgt(ret.data.url, done)
            }
          }, '', ['','']);
				}
			}
		}, appid)
	})
}

function downWgt(wgtUrl, done) {
	plus.nativeUI.showWaiting("...");
	plus.downloader.createDownload(wgtUrl, { filename: "_doc/gaosiupdate/" }, function (d, status) {
		if (status == 200) {
			installWgt(d.filename, done);	// 
		} else {
			plus.nativeUI.alert("" + JSON.stringify(status));
		}
		plus.nativeUI.closeWaiting();
	}).start();
}

// 
function installWgt(path, done) {
	plus.nativeUI.showWaiting("...");

	plus.runtime.install(path, { force: true }, function () {
		plus.nativeUI.closeWaiting();
		console.log("");
		plus.nativeUI.alert("",function () {
      if(Config.APP_ID == 3){
        //
        if (utils.isAndroid) {
            page.open(page.getLaunch());
            page.closeAll();
        } else {
            // ios plugins
            done && done();
        }
      }else{
        //
        done && done();
      }
			plus.runtime.restart();
		});
	}, function (e) {
		plus.nativeUI.closeWaiting();
		plus.nativeUI.alert("" + JSON.stringify(e));
	});
}



