import entry from 'entry';
import NAPI from 'nadapter';
import S from 'service';

import { Cell } from 'mint-ui';
import Topbar from 'modules/common/Topbar';
import ItemRemove from 'modules/common/ItemRemove';
import './style.less';

module.exports = entry({
  template: require('./index.tpl'),
  components: { Topbar, ItemRemove},

  data() {
    return {
        list: {},
        show:false,
        edit: {
            show: false
        },
        right: {
          text: '',
          handle: this.showEdit
        },
       
       selected:false
        
      };
  },
  computed: {
      allSelected() {
        return this.list.every(item => {
          return item.selected
        })
      },
  },

  methods: {
    showEdit() {
       this.edit.show = !this.edit.show;
       if(this.edit.show){
          this.right.text=""; 
        }else{
          this.right.text="";
       }
    },
    checkClick(index,item) {
      this.list[index].selected = !this.list[index].selected
      
    },
    selectAll() {

        if (this.allSelected) {

            this.list.forEach(item => {
                item.selected = false
            })
        } else {
           this.list.forEach(item => {
                item.selected = true
            })
        }
    },
    getSelectItems() {
            let _carts = []

            this.list.forEach(item => {
                
              if (item.selected) {
                  _carts.push( item.skuId)
              }
            })

            return _carts
        },
      delete() {
        console.log(this.getSelectItems())
        S.user.delwishList(this.getSelectItems()).then(ret => {
          if(ret.status==200){
              console.log("");
              this.list.$remove(item)
            }else{
              console.log("");
            }
        })
      },
    init() {
        S.user.wishList({}).then(ret => {
          console.log(ret.data)
          this.list = ret.data.map(item => {
                    
            item.selected = false
            return item
          })
         
        }).catch(err => {

        });
      },
  },
  ready() {
    this.init();
  }
});


