/**
 * native loading
 */
import utils from '../utils'

const _waiting = {}
const _loadQueues = {}

export default {
  
  /**
   * loading
   * @param id id
   * @param title: ( String )  
   * @param ptions: ( WaitingOptions )   
   */
  show (id, title, options) {

    id = id || 'loadingdefault'
    
    if (_loadQueues[id] && _loadQueues[id].length > 0) {
      _loadQueues[id].push(1)
      return _waiting[id]
    } else {
      _loadQueues[id] = [1]
    }

    // iosloading
    if(window.plus) {
      console.log(window.plus.nativeUI && window.plus.nativeUI.showWaiting);
      _waiting[id] = window.plus.nativeUI.showWaiting(title || '', options || {})
      _waiting[id].onclose = () => {
        _loadQueues[id] = []
      }
    }

  },
  
  /**
   * idloading
   */
  hide (id) {
    id = id || 'loadingdefault'

    if (_loadQueues[id]) {
      _loadQueues[id].pop()
    } else {
      this.hideAll()
    }
  },
  /**
   * loading
   */
  hideAll () {
    console.log('loaded  all')
  }
}


