<template>
	<div class="base-bottom item-bottom clear">
    <div class="bottom-left fl">
        <span @click="showEdit">
          <img v-show="!edit.show" src="../../assets/images/item/favourite.png" />
          <img v-show="edit.show" src="../../assets/images/item/favourite_on.png" />
        </span>
        <span @click="__gotoPage('index',{active:2})" class="span0"><img src="../../assets/images/item/shoppingcar.png"/><i>1</i></span>
    </div>
    <div class="bottom-right fr">
        <span class="span0 buy" @click="buy" ></span>
        <span  class="span1 " @click="add">{{title}}</span>
    </div>
	</div>

</template>    

<script>

  
  const props = {
    /**
     * 
     * @type {Object}
     */
    title: {
      type: String,
      default: ''
    }
  };

  const events = {
  	BUY: 'cart-buy',
  	ADD: 'cart-add'
  };

  export default {

    props: props,
    data() {
        return {
          edit: {
                show: false
            },
        };
      },
    methods: {
			buy() {
				this.$dispatch(events.BUY);
			},
      add() {
				this.$dispatch(events.ADD);
      },
      showEdit() {  
          this.edit.show = !this.edit.show;
      }
    },
    ready() {}
  };    
</script>

<style lang="less">
</style>


