/**
 * 
 */
import customConfig from '../../../config'

export default {
  /**
   * 
   * @param id id
   * @param label [object] 
   */
  send(id, label) {
    if(customConfig.APP_SCHEME !== 'teacher' && window.plus.statistic){
      window.plus.statistic.eventTrig(id, JSON.stringify(label))
    }
  }
}


