<template>
  <div class="mint-radiolist">
    <label class="mint-radiolist-title" v-text="title"></label>
    <x-cell v-for="option in options">
      <label class="mint-radiolist-label" slot="title">
        <span
          :class="{'is-right': align === 'right'}"
          class="mint-radio">
          <input
            class="mint-radio-input"
            type="radio"
            v-model="value"
            :disabled="option.disabled"
            :value="option.value || option">
            <span class="mint-radio-core"></span>
        </span>
        <span class="mint-radio-label" v-text="option.label || option"></span>
      </label>
    </x-cell>
  </div>
</template>

<script>
import XCell from './cell';
/**
 * mt-radio
 * @module components/radio
 * @desc  cell 
 *
 * @param {string[], object[]} options -  [{label: 'label', value: 'value', disabled: true}]  ['ab', 'cd', 'ef']
 * @param {string} value - 
 * @param {string} title - 
 * @param {string} [align=left] - checkbox `left`, `right`
 *
 * @example
 * <mt-radio :value.sync="value" :options="['a', 'b', 'c']"></mt-radio>
 */
export default {
  name: 'mt-radio',

  props: {
    title: String,
    align: String,
    options: {
      type: Array,
      required: true
    },
    value: String
  },

  components: {
    XCell
  }
};
</script>

<style lang="css">
  @import "./style/var.css";

  @component-namespace mint {
    @component radiolist {

      .mint-cell {
        padding: 0;
      }

      @descendent label {
        display: block;
      }

      @descendent title {
        font-size: 11px;
        margin: 8px;
        padding-left: 5px;
        display: block;
        color: $radio-title-color;
      }
    }

    @component radio {
      @when right {
        float: right;
      }

      @descendent label {
        vertical-align: middle;
      }

      @descendent input {
        display: none;

        &:checked {
          + .mint-radio-core {
            background-color: $color-blue;
            border-color: $color-blue;

            &::after {
              background-color: $color-white;
              transform: scale(1);
            }
          }
        }

        &[disabled] + .mint-radio-core {
          background-color: $color-grey;
          border-color: #ccc;
        }
      }

      @descendent core {
        display: inline-block;
        background-color: $color-white;
        border-radius: 100%;
        border: 1px solid #ccc;
        position: relative;
        size: 20px;
        vertical-align: middle;

        &::after {
          content: " ";
          border-radius: 100%;
          position: absolute 5px * * 5px;
          size: 8px;
          transition: transform .2s;
          transform: scale(0);
        }
      }
    }
  }
</style>



