import entry from 'entry';
import NAPI from 'nadapter';
import S from 'service';
import Topbar from 'modules/common/Topbar';
import ItemCard from 'modules/common/ItemCard';
import {CellSwipe, Cell } from 'mint-ui';
import './style.less';
module.exports = entry({
  template: require('./index.tpl'),
  components: { Topbar, ItemCard,
   [CellSwipe.name]: CellSwipe, [Cell.name]: Cell},

  data() {
    return {
        list: [],
        vallist: [],
        show:false,
        edit: {
            show: false
        },
         right: {
          text: '',
          handle: this.showEdit
        }
      };
  },
  computed: {
      allSelected() {
          return this.list.every(item => {
            return item.selected
          })
      },
  },

  methods: {
    showEdit() {
       this.edit.show = !this.edit.show;
       if(this.edit.show){
          this.right.text=""; 
        }else{
          this.right.text="";
       }
    },
    select(index) {
      console.log(index)
      console.log(this.vallist[0].isSelected)
       this.vallist[index].isSelected = !this.vallist[index].isSelected
    },
    delete(data,id,item) {
      console.log(id)
        S.user.delbrowseList({browseDate:data,itemId:id}).then(ret => {
          if(ret.status==200){
              console.log("");
              this.list.$remove(item)
            }else{
              console.log("");
            }
        })
    },
    getSelectItems() {
        let _carts = []
        this.list.forEach(item => {
          if (item.selected) {
              _carts.push(item.itemId)
          }
        })
        return _carts
    },
    delselected(){
     
      console.log(this.getSelectItems())
        S.user.delbrowseList(this.getSelectItems()).then(ret => {
        if(ret.status==200){
            console.log("");
            this.list.$remove(item);
          }else{
            console.log("");
          }
      })
    },
    init() {
        S.user.browseList({}).then(ret => {
        let _vallist = []
        this.list = ret.data
        for(var key in ret.data){
            if(!_vallist[key]) {
              _vallist[key] = []
            }

            _vallist[key].push(Object.assign(ret.data[key], {
              isSelected: false
            }))

        }
        this.vallist = _vallist
        console.log(this.vallist)
      })

    }
  },
  ready() {
    this.init();
  }
});


