<template>
  <div
    v-show="$parent.swiping || id == $parent.active"
    class="mint-tab-container-item">
    <slot></slot>
  </div>
</template>

<script>
/**
 * mt-tab-container-item
 * @desc  tab-container 
 * @module components/tab-container-item
 *
 * @param {number|string} [id] -  id
 *
 * @example
 * <mt-tab-container :active.sync="selected">
 *   <mt-tab-container-item id="1"> A </mt-tab-container-item>
 *   <mt-tab-container-item id="2"> B </mt-tab-container-item>
 *   <mt-tab-container-item id="3"> C </mt-tab-container-item>
 * </mt-tab-container>
 */
export default {
  name: 'mt-tab-container-item',

  props: ['id']
};
</script>

<style>
  @component-namespace mint {
    @component tab-container-item {
      flex-shrink: 0;
      width: 100%;
    }
  }
</style>



