/**
 * 
 * @author libaoxu@gaosiedu.com
 * @date 2016-11-26
 */

/**
 * key roleName, value, 
 * , ,, jsonkey, 
 * @type {Object}
 */
const ROLE_SETTINGS = {
  /**
   * @type {Object}
   * , key, superKey,
   * enmurable: {
   *  name {String} 
   *  key {String} key
   *  superKey {String} 
   *  highKey {String} 
   * }
   * curernt {Boolean} 
   * params {Object} 
   * privileges {Array} 
   */
  assistant: {
    id: '',
    current: false,
    enumerable: {
      name: '',
      key: 'assistant',
      // teacher, current
      highKey: 'teacher'
    }
  },

  littleBao: {
    current: false,
    params: {},
    id: '',
    privileges: [''],
    enumerable: {
      superKey: 'teacher',
      name: '',
      key: 'littleBao',
    }
  },

  isInWhiteList: {
    id: '',
    current: false,
    enumerable: {
      key: 'whiteList',
      name: '',
      superKey: 'teacher'
    }
  },

  isNovice: {
    id: '',
    current: false,
    enumerable: {
      key: 'isNovice',
      name: ''
    }
  },

  teacher: {
    id: '',
    current: false,
    enumerable: {
      name: '',
      key: 'teacher'
    }
  }
};

/**
 * , : isInWhiteList: true, user.roles
 * @type {Array}
 */
const EXTRA_ROLE_KEYS = ['isInWhiteList', 'isNovice'];

/**
 * 
 */
let roleManage = {

  roleInfo: null,

  get (user) {

    // 
    if (this.roleInfo) return this.roleInfo;

    this.roles = this.getRoles(user);

    this.roleDescriptes = this.getRoleDescriptes();

    this.setRoleSettingsWithDescriptor();

    return this.roleInfo = this.getRoleInfo();
  },

  /**
   * roles
   * 
   * @param {Object} user 
   */
  getRoles (user) {

    let roles = user.roles || [];

    EXTRA_ROLE_KEYS.forEach((roleKey) => {
      if (user[roleKey] === 1 || user[roleKey] === true) {
        roles.push({
          roleName: roleKey
        });
      }
    });    

    return roles;
  },

  /**
   * , Object.defineProperties(obj, this.roleDescriptes); 
   * 
   * @returns {Object}  {assistant: {id: {value: 123, enumerable: false}, roleName: {}}}
   */
  getRoleDescriptes () {

    /**
     * 
     * [{"roleName": "teacher", "id": "11"}]
     */
    let roles = this.roles;
    let roleDescriptes = {};

    roles.forEach((item) => {
      
      roleDescriptes[item.roleName] = {};

      for (let key in item) {
        // 
        roleDescriptes[item.roleName][key] = {
          value: item[key],
          enumerable: false
        }
      }
      
    });
    return roleDescriptes;
  },

  /**
   * 
   * @returns {Object} 
   */
  getRoleInfo () {
    let roleMap = {
      __proto__: {}
    };

    let prop;
    let roleKey;
    let roleItem;
    let  _value;
    let roleDescriptor;
    let roleDescriptes = this.roleDescriptes;

    for (prop in ROLE_SETTINGS) {
      roleItem = ROLE_SETTINGS[prop];

      // roleDescriptes, , prop
      roleDescriptor = roleDescriptes[prop];

      // roleKeypages
      roleKey = roleItem.key;
      
      if (roleDescriptor && typeof roleDescriptor === 'object') {

        /**
         * , , current
         */
        _value = roleDescriptes[roleItem.highKey] ? {}: {
          current: true
        };

        let inMapRole = roleMap[roleKey];
        // roleMap, , , 
        if (inMapRole && typeof inMapRole === 'object') {
          Object.assign(inMapRole, this.copyBySuper(roleItem, _value, roleMap));
        } else {
          roleMap[roleKey] = this.copyBySuper(roleItem, _value, roleMap);
        }

        // : 
        Object.defineProperties(roleMap[roleKey], roleDescriptor);
      } else {
        
        roleMap[roleKey] || (roleMap[roleKey] = roleItem);
      }

    }
    return roleMap;
  },

  /**
   * , roleItem  ROLE_SETTINGS[roleItem.superKey]
   * 
   * @param {Object} roleItem 
   * @param {Object} value 
   * @param {Object} roleMap getRoleInfo()(this.roleInfo)
   * @returns {Object}  {name: '', key: 'littleBao', superKey: 'whiteList, privileges: ['', ''], dataList: {}}
   */
  copyBySuper (roleItem, value, roleMap) {
    if (!roleItem) {
      return {};
    }

    let roleKey = roleItem.key;
    let superKey = roleItem.superKey;
    if (superKey) {
      
      if (!roleMap[superKey]) {
        roleMap[superKey] = ROLE_SETTINGS[superKey];
      } 

      let superRole = roleMap[superKey];
      if (superRole) {
        // roleItem, roleItem: superRole, value
        //  roleMap[superKey]
        mixValues(superRole, roleItem, value); 

        if (!superRole.superKey) {
          // roleItem
          return Object.assign(roleItem, superRole);
        } else {
          // , ROLE_SETTINGS[roleItem.superKey] 
          return Object.assign(roleItem, this.copyBySuper(superRole, value, roleMap));
        }
      } else {
        return roleItem;
      }
    } else {
      return roleMap[roleKey] = Object.assign(roleItem, value);
    }

  },

  /**
   * ROLE_SETTINGS , enumerable, role, 
   * @example   teacher: {enumerable: {name: '',key: 'teacher'}, active: true} => teacher: {name: '',key: 'teacher', enumerable: {name: '',key: 'teacher'}, active: true}}
   */
  setRoleSettingsWithDescriptor () {
    let role;
    let descriptor;
    let enmurableObj;

    for (let key in ROLE_SETTINGS) {
      if (ROLE_SETTINGS.hasOwnProperty(key)) {
        role = ROLE_SETTINGS[key];
        enmurableObj = role.enumerable;

        // 
        if (enmurableObj && typeof enmurableObj === 'object') {
          
          descriptor = {};
          for (let _key in enmurableObj) {
            descriptor[_key] = {
              value: enmurableObj[_key]
            }
          }

          // ROLE_SETTINGSenumerable
          descriptor.enumerable = {
            value: enmurableObj,
            enumerable: false
          };
          
          Object.defineProperties(role, descriptor);
        } 
      }
    }  
  }

};

/**
 * , , 
 * @return {Object} 
 * @example mixValues({a: ['']}, {a: [''], b:100}, {}) => {a: ['', ''], b: 100} 
 */ 
function mixValues (...rest) {
  let i = 1;
  let args = [...rest];
  let length = args.length;
  // 
  let target = args[0] || {};
  let options;
  // 
  let source;
  // 
  let copy;

  // 
  for (; i < length; i++) {

    if (options = args[i]) {

      for (name in options) {
        source = target[name];
        copy = options[name];

        // 
        if (source === copy) {
          continue;
        }

        if (Object.prototype.toString.call(copy) === '[object Array]') {
          // 
          target[name] = source && source.concat ? source.concat(copy) : copy;
        } else {
          // 
          target[name] = copy;
        }
      }

    }

  }

  return target;

}


/**
 *  __proto__ , __proto__.__proto__ __proto__
 * 
 * @param {Object} subObj 
 * @param {Object} superKey key
 * @param {Object} superValue 
 * @returns {Object} {key: 'whiteList', name: '', ..., __proto__:{teacher: {},  }
 */
function extendProto (subObj, superKey, superValue) {

  // _super
  subObj._super = superValue;
  
  // , __vm__.__roleInfo.littleBao.dataList = __vm__.__roleInfo.teacher.dataList
  subObj.__proto__ = superValue;

  // key,  __vm__.__roleInfo.littleBao._super ===   __vm__.__roleInfo.littleBao.whiteList
  Object.defineProperties(subObj.__proto__, {
    [superKey]: {
      value: superValue
    }
  });

  // , ,  __vm__.__roleInfo.littleBao.teacher ===   __vm__.__roleInfo.littleBao.whiteList.teacher
  subObj.__proto__.__proto__ = superValue.__proto__;

  return subObj;
}


export default {

  /**
   * , data, n data
   */
  data: null,

  /**
   * 
   * @param {Object} user 
   * @return {Object}
   * @example user: {"id":16713,"insId":25,"teacherName":"test", "isNovice":true, "isInWhiteList":true, "roles": [{"roleName": "teacher", "id": "11"}, {"roleName": "assistant", "id": "22"} ]}
   */ 
  get (user) {
    // 
    if (this.data) return this.data;

    // role. 
    if (!user || !user.roles) return roleManage.get({
      isInWhiteList:true,
      roles: [
        {
          roleName: 'teacher',
          id: user && user.id || ''
        }
      ]
    });

    return this.data = roleManage.get(user);
  }

};


