import entry from 'entry';
import NAPI from 'nadapter';
import S from 'service';
import { Cell } from 'mint-ui';
import Topbar from 'modules/common/Topbar';
import { Search } from 'mint-ui';
import './style.less';
module.exports = entry({
  template: require('./index.tpl'),
  components: { Topbar,[Cell.name]: Cell,[Search.name]: Search },
  data() {
    return {
        list: [],
        item:[],
        active: 0,
        activeIndex:-1,

    };
  },
  methods: {
    init() {
      S.user.help().then(ret => {
         this.list = ret.data;  
         console.log(this.list[0].pageSize);  
      });
       
    },
    select(active) {
        this.active = active;
        console.log(active)
        //let cid=this.list[0].helpLcat[active].c1Id;
        //console.log(cid)
        S.user.helpcat({cid:active}).then(ret => {
          console.info("ddd");
           if(ret.status==200){
              console.log("");
            }else{
              console.log("");
            } 
        });
    },
    question(index){
      if(this.activeIndex === index){
        this.activeIndex = -1;
      }else{
        this.activeIndex = index;
      }
    }
  },
  ready() {
    this.init();
  }
});


