import entry from 'entry';
import NAPI from 'nadapter';
import S from 'service';
import Topbar from 'modules/common/Topbar';
import AddressCard from './AddressCard';
import { CellSwipe} from 'mint-ui';
import './style.less';
module.exports = entry({
  template: require('./index.tpl'),
  components: { Topbar,AddressCard, 
      [CellSwipe.name]: CellSwipe
  },

  data() {
    return {
      list:[],
      edit: {
          show: false
      },
      right: {
        text: '',
        handle: this.showEdit
      },
    };

  },
  computed: {
      allSelected() {
        return this.list.every(item => {
          return item.selected
        })
      },
  },

  methods: {
    
    showEdit() {

        this.edit.show = !this.edit.show;
        if(this.edit.show){

            this.right.text=""; 
        }else{

            this.right.text="";
        }
    },
     
    select(index){
       this.list[index].selected = !this.list[index].selected

    },
    selectAll() {

        if (this.allSelected) {

          this.list.forEach(item => {
              item.selected = false
          })
        } else {
          this.list.forEach(item => {
              item.selected = true
          })
        }
    },
    getSelectItems() {
        let _carts = []

        this.list.forEach(item => {
          if (item.selected) {
              _carts.push(item.addrId)
          }
        })
        return _carts

    },
    delete(id,item) {
        S.user.deladdress({addrId:id}).then(ret => {
          if(ret.status==200){
              console.log("");
              this.list.$remove(item);
            }else{
              console.log("");
            }
        })
    },
    deleteselected() {
        //let item = Object.assign({},this.getSelectItems());
        console.log(this.getSelectItems())
        S.user.deladdress(this.getSelectItems()).then(ret => {
          if(ret.status==200){
              console.log("");
              //this.list.$remove(item)
            }else{
              console.log("");
            }
        })
    },
    init() {
      S.user.address().then(ret => {
          this.list = ret.data.map(item => {
                    
              item.selected = false
              return item
          })
        });
    }
  },
  ready() {
    this.init();
  }
});


