/**
 * native loading
 */
import utils from '../utils'
import Config from '../../../config'

const _waiting = {}
const _loadQueues = {}

const androidLoading = (isShow) => {
  window.plus.bridge.exec('plugingaosi', 'showLoading', [isShow])
}

export default {
  
  /**
   * loading
   * @param id id
   * @param title: ( String )  
   * @param ptions: ( WaitingOptions )   
   */
  show (id, title, options) {
    window.plus.nativeUI.showWaiting(title || '', options || {})

    id = id || 'loadingdefault'

    if (_loadQueues[id] && _loadQueues[id].length > 0) {
      _loadQueues[id].push(1)
      return _waiting[id]
    } else {
      _loadQueues[id] = [1]
    }

    /**
     * teacher, andriodloading
     * modify by libaoxu@gaosiedu.com 2016-12-01
     */
    // if (Config.APP_SCHEME === 'teacher' && utils.isAndroid) {
    //   androidLoading(true)
    // } else {
      _waiting[id] = window.plus.nativeUI.showWaiting(title || '', options || {})
      _waiting[id].onclose = () => {
        _loadQueues[id] = []
      }
    // }
  },
  
  /**
   * idloading
   */
  hide (id) {
    id = id || 'loadingdefault'

    if (_loadQueues[id]) {
      _loadQueues[id].pop()
      
      if (_loadQueues[id].length === 0) {
        // _waiting[id].close()
        this.hideAll()
      }
    } else {
      this.hideAll()
    }
  },
  /**
   * loading
   */
  hideAll () {
    // if (Config.APP_SCHEME === 'teacher' && utils.isAndroid) {
    //   androidLoading(false)
    // } else {
      window.plus.nativeUI.closeWaiting()
    // }
  },

  /**
   * plus.nativeUI.showWaitingWaiting
   * close: 
   * 
   * onclose: 
   * 
   * @param {String} id loading.show() 
   * @returns {Waiting} plus.nativeUI.showWaitingWaiting
   * @example http://www.html5plus.org/doc/zh_cn/nativeui.html#plus.nativeUI.Waiting
   */
  getWaitingById (id) { 
    return _waiting[id] || {};
  }
  
}


