/**
 * 
 */
import utils from '../utils';
import ui from './ui';
import user from './user';

// 
const TIMEOUT = 20000;
const CNT_TYPE = {
  JSON: 'application/json'
};

export default (opt, appId) => {
  let extra = utils.obj2Hash({
    // devicemodel: window.plus.device.model, // 
    // devicevendor: window.plus.device.vendor, // 
    // appid: '3.8.9',
    // token: '5184BDD1-E296-474A-9693-5D0FE6A53193'
  });

  opt = Object.assign({
    url: '',
    type: 'GET',
    cache: false,
    dataType: 'JSON',
    // contentType: '', // 
    data: {},
    success: function () { },
    error: function () { },
    headers: {}
  }, opt);

  ajax(opt, appId);
}

/**
 * ajax
 * */
function ajax(options, appId) {
  options = options || {};
  options.type = (options.type || 'GET').toUpperCase();
  options.dataType = options.dataType || 'json';

  let xhr;

  let params = utils.obj2Hash(options.data);
  if (CNT_TYPE.JSON === options.contentType) {
    params = options.data;
  }

  if (window.XMLHttpRequest) {
    xhr = new window.XMLHttpRequest();
  }
  // debugger
  // 
  xhr.timeout = TIMEOUT;

  xhr.onreadystatechange = function () {
    // 
    if (xhr.readyState === 4) {
      var status = xhr.status;

      if (status >= 200 && status < 300) {
        try {
          options.success && options.success(JSON.parse(xhr.responseText), xhr.responseXML);
        } catch (e) {
          options.error && options.error(status);
        }

        let _token = xhr.getResponseHeader('token');

        // token
        if (_token) {
          user.setToken(_token);
        }
      } else {
        options.error && options.error(status);
      }
    }
  };

  // 
  xhr.ontimeout = function () {
    options.error && options.error(-1);
  };

  xhr.open(options.type, options.url, true);

  if (options.type == 'GET') {
    if (params) {
      if (options.url.indexOf('?') > -1) { // 
        options.url += ('&' + params);
      } else {
        options.url += ('?' + params);
      }
    }
    xhr.setRequestHeader('token', user.getToken());
    xhr.setRequestHeader('appId', appId || '2');
    xhr.setRequestHeader('X-Requested-With', 'XMLHttpRequest');
    xhr.send(null);
  } else {
    // 
    xhr.setRequestHeader('token', user.getToken());
    xhr.setRequestHeader('appId', appId || '2');
    xhr.setRequestHeader('Content-Type', 'text/plain;charset=UTF-8');
    xhr.setRequestHeader('X-Requested-With', 'XMLHttpRequest');
    xhr.send(JSON.stringify(options.data));
  }
  
}


