/**
 * hbuilder 
 */

export default {
  /**
   * 
   * @param title [string] 
   * @param content [string] 
   * @param href [string] 
   * @param scene [string] 
   * @param pictures [string]   
   * @param thumbs [string] 
   * @return Promise
   */
  shareWX(title, content, href, scene, pictures, thumbs){
    return new Promise((res, rej) => {
      this.getShareService(shares => {
        shares.forEach(item => {
          if(item.id === 'weixin'){
            item.send({
              title: title || '',
              content: content || '',
              href: href || '',
              extra: {
                scene: scene || 'WXSceneSession'
              }
            }, res, rej)
          }
        })
      })
    })
  },

  /**
   * 
   */
  getShareService(done){
    plus.share.getServices(done, function(e){
      done([])
    })
  }
}


