/**
 * 
 */

import routes from '../../routes'
import Config from '../../config';
import NAPI from 'nadapter';

export default (router) => {
  /**
   * 1 
   */
  router.map(routes)

  /**
   * 2 
   */
  router.beforeEach(function (transition) {
    /**
     * 
     */
    let routerInfo = transition.to;

    if (false) {
      NAPI.page.open('login')
    }
    
    transition.next();
  })

}


