const BARCODE = 'plugingaosi'

const plugin = {
  getBridge() {
    return window.plus.bridge
  },
  /**
   * 
   */
  compress(path, cb, fail) {
    this.getBridge().exec(BARCODE, 'compressImage', [this.getBridge().callbackId(cb, fail), path, '375', '0.8'])
  },
  /**
   * 
   * @param type [number] 0: 1:
   */
  cameraAndGalleryAuth(type,cb,fail) {
     this.getBridge().exec(BARCODE, 'cameraAndGalleryAuth', [this.getBridge().callbackId(cb, fail),type])
    // return this.getBridge().execSync(BARCODE, 'cameraAndGalleryAuth', [type])
  }
}

/*
* 
*/

// 
function getUid(){
  return Math.floor(Math.random()*100000000+10000000).toString();
}
export default {
  /*
  * 
  * @param server (String)  
  * @param param (Object)   { method: 'POST', files: []}
  * @param cb 
  */
  upload(server, param, cb ) {
    let files = param.files || [];
    if(files.length<=0){
      plus.nativeUI.alert('');
      return;
    }
    const wt = plus.nativeUI.showWaiting();
    const task = plus.uploader.createUpload(server,
      {method: param.method},
      function(t,status){ //
        if(status==200){
            wt.close();
            cb && cb(t);
        }else{
          plus.nativeUI.alert('');
          wt.close();
        }
      }
    );

    task.addData('client','HelloH5+');
    task.addData('uid', getUid());
    
    files.forEach(item => {
      task.addFile(item.path, { key: item.name });
    });
    
    task.start();
  },
  /*
  * 
  * @param cb  file
  */

    pickByCamera(cb, error) {
      plugin.cameraAndGalleryAuth('0',
      //
      (e)=> {
        if(e == 1){
          plus.camera.getCamera()
            .captureImage(
                function(p){
                // cb && cb(p);
                //
                plus
                  .io
                  .resolveLocalFileSystemURL( p,  ( entry ) => {
                    cb(entry.toLocalURL());
                  });
              },
              error || function(error){
                window.console.log(JSON.stringify(error));
              }
            );
        }else{
          plus.nativeUI.alert('');
        }
      });
  },
  /*
  * 
  * @param cb Function
  * @param cancel Function
  * @param param Object  or 
  */
  pickByGallery(cb, cancel, param) {
    plugin.cameraAndGalleryAuth('1',
    (e)=> {
      if(e == 1){
        let filter = Object.assign({ 
                    filter:'image',
                    multiple: true,
                    system: false,
                  }, param);
        plus.gallery.pick((p)=>{
              cb && cb(p);
            },(e)=>{
              cancel && cancel(e);
            }, filter);
      }else{
        plus.nativeUI.alert('');
      }
    });
   
  },
  /*
  * 
  * @param path String
  * @param cb Function
  * @param isNative BooleannativefalseH5
  */
  compress(path, cb, isNative) {
    window.console.log('isNative',isNative);
    if (isNative) {
      this._compress(path, cb);
    } else {
      this.compressH5(path, cb);
    }
  },
  _compress(path, cb) {
    plugin.compress(path, cb, () => {
      alert('error');
    });
  },
  compressH5(path, cb) {
    const QUALITY = 0.8; //
    const W = 1080; //
    let base64 = '';
    let img = new Image();
    img.src = path;
    img.onload = function () {
      let w = this.width,
          h = this.height,
          scale = w / h; 
          w = W || w;
          h = w / scale;
      let canvas = document.createElement('canvas');
      let ctx = canvas.getContext('2d');
      canvas.setAttribute('height', h);
      canvas.setAttribute('width', w);
      ctx.drawImage(this, 0, 0, w, h);
      base64 = canvas.toDataURL('image/jpeg', QUALITY );
      cb && cb(base64);
    }
  }
};


