const BARCODE = 'plugingaosi'

const plugin = {
  getBridge() {
    return window.plus.bridge
  },
  /**
   * 
   */
  pictureZoom(path, cb, fail) {
    this.getBridge().exec(BARCODE, 'pictureZoom', [this.getBridge().callbackId(cb, fail), path])
  }
}

export default {
 /**
  * 
  */
  pictureZoom(path) {
    plugin.pictureZoom(path,() => {
      window.console.log(path);
    });
  }
};


