export default (Vue) => {
  /**
   * 
   * text | cuttext '4'
   */
  Vue.filter('cuttext', function (value) {
    if (value.length > 4) {
      return value.substr(0, 4) + '..'
    }

    return value
  });

  Vue.filter('sex', function (value) {
    
    if(value == "m") {
      return "";
    } else if(value == "f"){
      return "";
    }else{
      return "";
    }
  });
}



