import entry from 'entry';
import NAPI from 'nadapter';
import S from 'service';

import Topbar from 'modules/common/Topbar';
import { Toast } from 'mint-ui';
import './style.less';

const INTERVAL_VALUE = 60
let timeInterval = null

const REINTERVAL_VALUE = 30
let timeReinterval = null

module.exports = entry({
  template: require('./index.tpl'),
  components: { Topbar },

  data() {
    return {
      active:0,
      sendInfo:[],
      mobile: '',
      code:'',
      mobile1: '',
      code1:'',
      status:0,
      interval: 0,
      reinterval: 0
    };
  },

  methods: {
    init() {
    },
    select(active){
      this.active = active
    },
     sendCode(){
      //'13521957015'
        if(!(/^1[34578]\d{9}$/.test(this.mobile))){ 
            toast("");  
            return false; 
        }else{
          this.interval = INTERVAL_VALUE;
          this.timing()
          S.user.send({mobile:this.mobile}).then(ret => {
            
            if(ret.status==200){
              console.log("");
            }else{
              console.log("");
            }
        
          });
        } 
        
    },
    resendCode(){
      //'13521957015'
        if(!(/^1[34578]\d{9}$/.test(this.mobile))){ 
            toast("");  
            return false; 
        }else{
          this.reinterval = REINTERVAL_VALUE;
          this.retiming()
          S.user.send({mobile:this.mobile}).then(ret => {
            
            if(ret.status==200){
              console.log("");
            }else{
              console.log("");
            }
        
          });
        } 
        
    },
    timing() {
      timeInterval = setInterval(() => {
        if(this.interval === 0) {
          return clearInterval(timeInterval)
        }
        this.interval -= 1
      }, 1000)
    },
    retiming() {
      timeReinterval = setInterval(() => {
        if(this.reinterval === 0) {
          return clearInterval(timeReinterval)
        }
        this.reinterval -= 1
      }, 1000)
    },
    bind(){
      if(this.mobile=="" || !/^\d{6}$/.test(this.code)){
        //(/^\d{6}$/.test(this.code))
       Toast("");
        return false;
      }else{
        S.user.bind({mobile:this.mobile,valiCode:this.code}).then(ret => {
           this.status = ret.status;
          if(ret.status==200){
            console.log("");
          }else if(ret.status==404){
            console.log("");
          }else if(ret.status==500){
            console.log("");
          }
        });
      }
    }
  },
  ready() {
    this.init();
  }
});


