<template>
	<div class="modl-catnav">
    <ul :style="{width: innerWidth + 'px'}"  class="clear">
      <li @click="select($index)" v-for="item in items">
        <a :class="{'current': active === $index}">{{item.name}}</a>
      </li>
    </ul>
	</div>
</template>    

<script>
  const props = {
    items: {
      type: Array,
      default: []
    }
  };

  export default {
    props: props,
    data() {

      return {
        innerWidth: 600,
        active:0,
      }
    },
    methods: {
      select(index) {
        this.active = index
      },
    },
    ready() {
      this.innerWidth = this.items.length * 40
    }
  };    
</script>

<style lang="less">
  @import "../../pages/common.less";
  @import "../../pages/boy.less";
  .modl-catnav{
    width: 100%;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    ul{
      height: 40px;
      line-height: 40px;
      padding:0px;
      background: #fff;
      overflow: hidden;
     
      li{
        float: left;
        height: 100%;
        border-bottom: 1px solid #eee;
        a{
          margin-left: 16px;
          margin-right: 10px;
          display: block;
          height: 40px;
          color: @com-subtitle;
          padding: 0 1px;
         /* &:active{
            background:#fff;
          };*/
        }
        .current{
          border-bottom: 3px solid @boy-color;
          color: @boy-color;
          
        }
      }
      &:after{
        content: '';
        display: block;
        clear: both;
      }
    }
  }
</style>


