<template>
  <div v-show="!edit">
    <div class="base-add-none" v-if="item.length==0"></div>
    <mt-cell-swipe  v-for="goods in item" :right="[
          {
            content: '',
            style: { background: '#d70000', color: '#fff',lineHeight: '75px',},
            handler: () => {this.delete(goods.skuId,goods)}
          }
        ]">
        <div class="base-add0 clear base-browser-record" id="base-es" style="height:74px;">
          <item-card :item.sync="{'title': goods.title, 'subtitle': goods.subtitle, 'image': goods.image, 'price':goods.price}"></item-card>
      </div>
       <div class="base-add-clear dn"><i></i></div>
    </mt-cell-swipe>
 </div>
 <div v-show="edit">
    <div v-for="goods in item" class="clear base-browser-record" id="base-ess" style="height:74px;">
        <div class="base-point" @click="select($index)"><i v-if="goods.selected"></i></div>
        <item-card 
        :item.sync="{'title': goods.title, 'subtitle': goods.subtitle, 'image': goods.image, 'price':goods.price}"
        style="padding-left: 30px;"
        ></item-card>
    </div>
 </div>
</template>
<script>
    import S from 'service';
    import ItemCard from 'modules/common/ItemCard';
    import {CellSwipe, Cell } from 'mint-ui';
    const props = ['item', 'edit', 'none',];
    const events = {
      CHECK_CLICK: 'check-click',
      DELETE: 'delete'
    };

  export default {
    props: props,
    components: { ItemCard,
       [CellSwipe.name]: CellSwipe, [Cell.name]: Cell
    },

    data() {
        return {
            list: [],
            item:[],
            
        };
     
    },
    methods: {

      select(index) {
        this.$dispatch(events.CHECK_CLICK, index );
      },
      delete(id,item) {
        this.$dispatch(events.DELETE, item);
        
        S.user.delwishList({skuId:id}).then(ret => {
          if(ret.status==200){
              console.log("");
              this.item.$remove(item)
            }else{
              console.log("");
            }
        })
        
      },
      init() {
        
      },
     
  },
    ready() {
        this.init();


    }
  };   

</script>
<style type="text/css">
.base-browser-record{
 background: #fff;
 margin: 0 auto;
 position: relative;
 width: 290px;
 border-bottom:0.5px solid #eee;
}
.base-browser-record:last-child{border-bottom: none;}
.base-browser-record .img{
 width: 55px; 
 height: 55px;
 text-align: center; 
 line-height:55px; 
 margin-left:0px;
 margin-top: 10px;
 }
.base-browser-record img{
  max-height: 55px;
  max-width: 55px; 
  vertical-align: middle; 
}

#base-es span{
  width:58px; 
  height: 18px; 
  border-radius: 2px; 
  background: #fff; 
  line-height: 18px; 
  text-align: center;
  color: #000; 
  border: 0.5px solid #eee; 
  font-size: 8px;
  position: absolute;
  right: 10px;
  top:48px;
  }
#base-es span:active{background:#FAFAFA;}
#base-ess{width: 320px;}
</style>


