<template lang="html">
  <!-- headerheader -->
  <mt-header v-if="!showHeaderEmpty" :fixed="fixed" :title="title" :style="style">
    <div slot="left" v-if="showLeft">
      <div class="topbar-back" @click="handleBack">
        {{backText}}
      </div>
      <mt-button v-if="showClose" @click="handleClose"></mt-button>
    </div>
    <div v-show="slotTitle" class="mint-header-title" slot="title">
      <slot></slot>
    </div>
    <div slot="right">
      <mt-button
        v-if="!!right"
        type="greenRadius"
        size="small"
        :icon="right.icon"
        :class="{'disabled': right.disabled, 'icon': !!right.icon}"
        :disabled="right.disabled"
        @click="right.handle" >
        {{ right.icon ? '' : right.text }}
      </mt-button>
    </div>
  </mt-header>
  <!-- headerheader -->
  <div class="header-empty" v-if="showHeaderEmpty" :style="[headerEmptyStyle,headerEmptyHeight]"></div>
  <!-- headerfixedtruediv -->
  <div class="comp-statusbar" v-if="!showHeaderEmpty && fixed" :style="{height: statusbarHeight}"></div>
</template>
<script>
  import {
    Header,
    Button
  } from 'mint-ui';
  import NAPI from 'nadapter';
  export default {
    name: 'topbar',
    components: {
      [Header.name]: Header,
      [Button.name]: Button
    },
    props: {
      //
      title: String,
      slotTitle: Boolean,
      type: {
        type: String,
        default: 'back'
      },
      // 
      showLeft: {
        type: Boolean,
        default: true
      },
      // 
      fixed: Boolean,
      // 
      showClose: {
        type: Boolean,
        default: false
      },
      // 
      backText: String,
      // 
      autoback: {
        type: Boolean,
        default: true
      },
      right: {
        type: Object
      },
      /**
       * header 
       * topbarfalse,topbartrue
       */
      showHeaderEmpty:{
        type: Boolean,
        default: false
      },
      /**
       * mt-header
       */
      style:{
        type:Object
      },
      /**
       * header
       * 
       */
      headerEmptyStyle:{
        type: Object
      },
      /**
       * header 
       * 
       */
      headerEmptyHeight:{
        type: Object
      }
    },
    methods: {
      handleBack() {
        if (this.autoback) {
          NAPI.page.back();
        }
        this.$dispatch('back');
      },
      handleClose() {
        this.$dispatch('close');
      },
      handleRight() {
        this.$dispatch('clickRight');
      }
    },
    data() {
      return {
        statusbarHeight: '40px'
      };
    },
    ready() {
    }
  };
</script>
<style lang="less">
  .mint-header.is-fixed {
    z-index: 9;
  }
  .mint-header {
    background-color: #fff;
    color: #000;
    height: 36px;
    border-bottom:solid 0.5px #eee;
    padding-top: 13px;
  }
  .topbar-back {
    display: inline-block;
    width: 9px;
    height:9px;
    margin-left: 8px;
    border-left: solid 1px #4a4a4a;
    border-top: solid 1px #4a4a4a;
    -webkit-transform: rotate(-45deg);
  }
  .mint-header-title {
    font-size:0.9rem;
    font-weight: normal!important
  }
  .comp-statusbar{
    background: #fff;
  }
</style>


